import React from 'react'
import { MenuItem, Select } from '@mui/material';
import { selectItemStyle } from './selectStyle';
import { RegionList } from '../../../utils/basic/getRegion';


const RegionSelect = ({ list, value, onChange}) => {
  
  return (
    <>
      <Select 
        sx={{width:'200px', height:'40px'}}
        size='small'
        value={value}
        onChange={onChange}
      >
        <MenuItem value={-1} sx={selectItemStyle}>전체</MenuItem>
        { React.Children.toArray(
          list?.map((item) =>
            <MenuItem value={item} sx={selectItemStyle}>{item}</MenuItem>
          )
        )}
      </Select>
    </>
  )
}

export default RegionSelect