import React from 'react'
import { MenuItem, Select } from '@mui/material';
import { getCurrYear } from '../../../utils/basic/getDate';
import { selectItemStyle } from './selectStyle';


const YearSelect = ({ value, onChange}) => {
  const year = getCurrYear()
  const yearList = [year, year-1]
  
  return (
    <>
      <Select 
        sx={{width:'140px', height:'40px'}}
        size='small'
        value={value}
        onChange={onChange}
      >
        { React.Children.toArray(
          yearList?.map((yy) =>
            <MenuItem value={`${yy}`} sx={selectItemStyle}>{yy}년</MenuItem>
          )
        )}
      </Select>
    </>
  )
}

export default YearSelect