import React, { lazy, useCallback, useState } from 'react';
import { Typography } from '@mui/material';
import YearSelect from '../../../components/common/Select/YearSelect';
import MonthSelect from '../../../components/common/Select/MonthSelect';
import { useDebug } from '../../../hooks/basic/useDebug';
import { useSelect } from '../../../hooks/basic/useSelect';
import { getCurrMonth, getCurrYear } from '../../../utils/basic/getDate';
import RegionSelect from '../../../components/common/Select/RegionSelect';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import userInfoAtom from '../../../recoil/atom/login/userInfoAtom';
import { useQuery } from '@tanstack/react-query';
import { getAssociationLoginInfo } from '../../../api/Association/info';
import LoadingModal from '../../../components/UI/Atoms/Spinner/LoadingModal';
import assoInfoAtom from '../../../recoil/atom/association/assoInfoAtom';

const MonthlyUserCount = lazy(() => import('./Chart/MonthlyUserCount'))
const GenderAgeStatistic = lazy(() => import('./Chart/GenderAgeStatistic'))
const StudentDojangStatistic = lazy(() => import('./Chart/StudentDojangStatistic'))
const LevelStudentStatistic = lazy(() => import('./Chart/LevelStudentStatistic'))
const PoomStudentStatistic = lazy(() => import('./Chart/PoomStudentStatistic'))

const AssociationHome = () => {
  let yy = getCurrYear().toString()
  let mm = getCurrMonth().toString()

  // recoil
  const userInfo = useRecoilValue(userInfoAtom)
  const assoInfo = useRecoilValue(assoInfoAtom)

  const asso = assoInfo.name === '한국' ? -1 : assoInfo.name

  // custom hook
  const [ year, handleYear ] = useSelect(yy)
  const [ month, handleMonth ] = useSelect(mm)
  const [ region, handleRegion ] = useSelect(asso)

  // useDebug(`${year} ${month} ${region}`)
  // useDebug(assoInfo)

  return (
    <>
      <div className="h-[60px] shadow-md rounded-[10px] flex items-center px-[20px] mb-[10px] bg-[#fff] gap-[10px]">
        <Typography variant="h4" className="font-bold mr-[20px]">
          현황
        </Typography>
        <YearSelect value={year} onChange={handleYear} />
        <MonthSelect value={month} onChange={handleMonth} />
        { assoInfo?.name === '한국' && <RegionSelect list={assoInfo?.region_info} value={region} onChange={handleRegion} />}
      </div>
      <div className="flex justify-center mx-auto gap-[20px]">
        <div className="flex flex-col items-center gap-[20px]">
          <div className="flex gap-[20px]">
            <div className="shadow-md rounded-[10px] bg-[#fff]">
              <MonthlyUserCount year={year} month={month} region={region} /> 
            </div>
            <div className="shadow-md rounded-[10px] bg-[#fff]">
              <GenderAgeStatistic year={year} month={month} region={region} /> 
            </div>
          </div>
          <div className="text-center mb-[30px] shadow-md rounded-[10px] bg-[#fff]">
            <StudentDojangStatistic year={year} month={month} region={region} /> 
          </div>
        </div>
        <div className="flex flex-col gap-[20px]">
          <div className="shadow-md rounded-[10px] bg-[#fff]">
            <LevelStudentStatistic year={year} month={month} region={region} /> 
          </div>
          <div className="shadow-md rounded-[10px] bg-[#fff]">
            <PoomStudentStatistic year={year} month={month} region={region} /> 
          </div>
        </div>
      </div>
    </>
  );
};

export default AssociationHome;
