import React from 'react'
import { MenuItem, Select } from '@mui/material';
import { monthList, } from '../../../utils/basic/getDate';
import { selectItemStyle } from './selectStyle';


const MonthSelect = ({ value, onChange}) => {
  
  return (
    <>
      <Select 
        sx={{width:'140px', height:'40px'}}
        size='small'
        value={value}
        onChange={onChange}
      >
        { React.Children.toArray(
          monthList?.map((mm) =>
            <MenuItem value={mm} sx={selectItemStyle}>{mm}월</MenuItem>
          )
        )}
      </Select>
    </>
  )
}

export default MonthSelect